@import url(https://fonts.googleapis.com/css?family=Acme|Josefin+Sans|Muli);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body {
  font-family: 'Josefin Sans', sans-serif;
  background-color: #D9D9D9;
  text-align: center; }

img {
  margin: 10px; }

h1 {
  margin-top: 150px;
  font-size: 5vw;
  color: #4484CE;
  font-family: 'Acme', sans-serif;
  font-weight: bolder; }

h2 {
  font-size: 25px;
  font-family: 'Acme', sans-serif;
  font-weight: bolder; }

p {
  text-align: center;
  font-style: italic;
  margin-top: 30px;
  font-size: 20px; }

label {
  text-align: center;
  font-style: italic;
  margin-top: 30px;
  font-size: 20px;
  font-weight: bolder; }

form {
  margin-top: 30px; }

h4 {
  margin-bottom: 10px; }

li {
  border: solid 1px;
  margin: 1px;
  padding: 2px; }

.result-container {
  text-align: center;
  display: flex;
  width: 90%;
  margin: 20px auto;
  border: solid; }

.result {
  margin: 10px auto;
  width: 100%; }

.result:nth-child(2) img {
  height: 75px; }

.result:nth-child(4) img {
  height: 150px; }

